.prime-c {
  display: none;
  background-color: rgba(15, 19, 22, 0.95);
  position: fixed;
  z-index: 110000;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  text-align: center;
  overflow: hidden;
}

.prime-form {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  height: 100%;
  width: 100%;
  position: relative;
}

.prime-form .form {
  position: relative;
  width: 100%;
  padding: 0;
}

.prime-form .form_name {
  font-weight: 700;
  text-align: left;
  font-size: 1.875rem;
  line-height: 1.1;
  margin-bottom: 1.5rem;
  color: #fff;
}

.prime-form .form_content {
  font-weight: 400;
  text-align: left;
  font-size: 22px;
  line-height: 1.34;
  margin-bottom: 1.5rem;
  color: #fff;
}

.prime-form .form-close {
  position: absolute;
  top: 40px;
  right: 40px;
  width: 20px;
  height: 20px;
  opacity: 0.4;
  transform: translate(50%, -50%);
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}

.prime-form .form-close:hover {
  opacity: 1;
  width: 25px;
  height: 25px;
}

.prime-form .form-contact {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  margin: 0 0 -35px -70px;
  width: 100%;
  height: 100%;
}

@media (max-width: 768px) {
  .prime-form .form-contact {
    flex-direction: column;
  }
}

.prime-form .form-contact-block {
  width: calc((100% - 70px * 2) / 2);
  margin: 0 0 35px 70px;
  position: relative;
  display: flex;
  flex-direction: column-reverse;
  margin-bottom: 1em;
}

@media (max-width: 768px) {
  .prime-form .form-contact-block {
    width: 100%;
  }
}

.prime-form .form-contact-block input:not(:placeholder-shown) + label,
.prime-form .form-contact-block input:focus + label,
.prime-form .form-contact-block input:-webkit-autofill ~ label {
  top: -15px;
  cursor: pointer;
}

.prime-form .form-contact-block .form-contact-input:focus + .form-contact-label,
.prime-form .form-contact-block .form-contact-input:not(:empty) + .form-contact-label,
.prime-form .form-contact-block .form-contact-input:hover + .form-contact-label {
  top: -15px;
}

.prime-form .form-contact input[required] ~ label::after {
  content: "*";
  display: inline-block;
  color: red;
}

.prime-form .form-contact-label {
  flex: none;
  color: #666666;
  position: absolute;
  top: 15px;
  left: 0;
  transition: top .3s ease;
  pointer-events: none;
}

.prime-form .form-contact-input {
  border-radius: 0;
  appearance: none;
  padding: 10px 0;
  min-height: 47px;
  transition: border-color .3s ease;
  background: transparent;
  border: 0;
  border-bottom: 1px solid #666;
  color: #fff;
  outline: none;
}

.prime-form .footer-form {
  margin: 35px 0 35px 70px;
}

.content {
  margin: 0;
  position: relative;
}

.section {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.section__title {
  margin-top: 3rem;
  text-transform: uppercase;
  font-weight: 600;
  font-size: 46px;
}

.section__text {
  font-weight: 400;
  margin-top: 1rem;
  font-size: 22px;
  line-height: 1.46;
}

.main-slide {
  position: relative;
  z-index: 10;
  overflow: hidden;
}

.main-slide::after {
  content: '';
  position: absolute;
  display: block;
  top: -1px;
  left: -1px;
  right: -1px;
  bottom: -1px;
  background: url(../img/mossman-banner-bg.svg) no-repeat bottom;
  background-size: contain;
  pointer-events: none;
  z-index: 30;
}

.main-slide_block {
  width: 100%;
  height: 100vh;
}

@media (max-width: 768px) {
  .main-slide_block {
    height: 80vh;
  }
}

.main-slide_img {
  position: relative;
  height: 100vh;
}

@media (max-width: 768px) {
  .main-slide_img {
    height: 80vh;
  }
}

.main-slide_img::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: block;
  background: rgba(0, 0, 0, 0.44);
}

.main-slide_img img {
  width: 100%;
  object-fit: cover;
  height: 100vh;
}

@media (max-width: 768px) {
  .main-slide_img img {
    height: 80vh;
  }
}

.main-slide .main-nav {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 100%;
  display: flex;
  align-items: center;
  z-index: 110;
}

.main-slide .main-nav .nav_menu {
  position: relative;
  width: 80%;
  line-height: 1.64;
  color: #fff;
  font-weight: 700;
  font-size: 56px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  justify-items: center;
  align-items: flex-start;
  margin-top: 0;
}

@media (max-width: 768px) {
  .main-slide .main-nav .nav_menu {
    width: 100%;
  }
}

.main-slide .main-nav .nav_menu-item {
  height: 100%;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
  color: #fff;
  font-size: 56px;
}

@media (max-width: 1190px) {
  .main-slide .main-nav .nav_menu-item {
    font-size: 36px;
  }
}

@media (max-width: 768px) {
  .main-slide .main-nav .nav_menu-item {
    font-size: 28px;
  }
}

.main-slide .main-nav .nav_menu-item:hover {
  color: #d41a3b;
  transform: translateX(10px);
}

.menu-form {
  margin-top: 50px;
}

.form-button {
  background-color: #d41a3b;
  color: #fff;
  font-size: 18px;
  font-weight: normal;
  padding: 20px;
  border: none;
  outline: none;
  box-shadow: none;
}

.slider {
  margin: 90px 0;
  position: relative;
  height: 500px;
  overflow: hidden;
}

@media (max-width: 768px) {
  .slider {
    height: 350px;
  }
}

@media (max-width: 540px) {
  .slider {
    height: 320px;
  }
}

.swiper-button {
  width: 40px;
  height: 40px;
  border-radius: 55%;
  top: calc(50% - 20px);
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  z-index: 100;
  background: url(../img/ico-arrow-white.svg) no-repeat center;
  background-size: 20px;
  background-color: #d41a3b;
}

.swiper-button-prev {
  left: calc(100% / 3 - 21px);
  transform: rotate(180deg);
}

@media (max-width: 768px) {
  .swiper-button-prev {
    left: calc(100% / 10 - 21px);
  }
}

.swiper-button-next {
  right: calc(100% / 3 - 21px);
  z-index: 30;
}

@media (max-width: 768px) {
  .swiper-button-next {
    right: calc(100% / 10 - 21px);
  }
}

.swiper-container {
  height: 100%;
  margin-left: auto;
  margin-right: auto;
  position: relative;
  overflow: hidden;
  list-style: none;
  padding: 0;
  /* Fix of Webkit flickering */
  z-index: 19;
  background-color: transparent;
}

.swiper-container::before {
  content: '';
  display: block;
  left: calc(100% / 3);
  top: 0;
  position: absolute;
  width: 1px;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.4);
  z-index: 20;
}

.swiper-container::after {
  content: '';
  display: block;
  top: 0;
  right: calc(100% / 3);
  position: absolute;
  width: 1px;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.4);
  z-index: 20;
}

@media (max-width: 768px) {
  .swiper-container::after {
    display: none;
  }
  .swiper-container::before {
    display: none;
  }
}

.swiper-wrapper {
  position: relative;
  width: 100%;
  height: 100%;
  z-index: 1;
  display: flex;
}

.swiper-image {
  width: 100%;
  overflow: hidden;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  padding: 0;
  height: 100%;
}

.swiper-image img {
  display: block;
  width: 100%;
  object-fit: cover;
  height: 100%;
}

.swiper-image .swiper-slide-duplicate {
  opacity: 0;
}

.swiper-slide {
  text-align: center;
  font-size: 18px;
  width: 100%;
  height: 100%;
  flex-shrink: 0;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-end;
  color: white;
}

.swiper-slide_name {
  font-size: 32px;
  font-weight: 700;
}

.swiper-slide_text {
  font-size: 18px;
  font-weight: 400;
}

.swiper-slide .slide-block {
  width: 100%;
  height: 120px;
  padding: 15px 40px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  background-color: rgba(0, 0, 0, 0.5);
  transition: opacity 0.3 ease-in-out;
}

.swiper-slide .slide-block .swiper-slide_name {
  opacity: 0.5;
}

.swiper-slide .slide-block .swiper-slide_text {
  opacity: 0;
  transition: opacity 0.3 ease-in-out;
}

.swiper-slide-active {
  transition: opacity 0.3 ease-in-out;
}

.swiper-slide-active .slide-block {
  transition: opacity 0.3 ease-in-out;
}

.swiper-slide-active .slide-block .swiper-slide_name {
  opacity: 1;
}

.swiper-slide-active .slide-block .swiper-slide_text {
  opacity: 1;
  transition: opacity 0.3 ease-in-out;
}

.company {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  gap: 30px;
}

.company-info {
  width: 50%;
}

.company_title {
  font-weight: 600;
}

.company_text {
  font-weight: 400;
}

.company_location {
  width: 100%;
}

.company .location-adress {
  font-weight: 400;
  font-size: 18px;
}
